<template lang="html">
  <div class="hd-input">
    <label class="hd-input-label" :for="unique" v-text="label"></label>
    <input class="hd-input-core" v-if="password" type="password" v-model="model" :id="unique" :placeholder="placeholder" :maxlength="maxlength" :disabled="disabled" @change="onchange">
    <input class="hd-input-core" v-else type="text" v-model="model" :id="unique" :placeholder="placeholder" :maxlength="maxlength" :disabled="disabled" @change="onchange">
    <div class="hd-input-clear" v-show="hasText" @click="clearText">
      <svg-error size="0.3" color="#ccc"></svg-error>
    </div>
  </div>
</template>

<script>
import SvgError from 'components/svg/error.vue'

export default {
  name: 'hd-input',
  components: {
    SvgError
  },
  props: {
    unique: String,
    password: Boolean,
    label: {
      type: String,
      required: true
    },
    value: {
      type: [String, Number],
      default: ''
    },
    placeholder: String,
    maxlength: [Number, String],
    disabled: Boolean
  },
  data () {
    return {
      model: this.value
    }
  },
  computed: {
    hasText () {
      return !this.disabled && this.model !== ''
    }
  },
  watch: {
    value (newValue) {
      this.model = newValue
    }
  },
  methods: {
    clearText () {
      this.model = ''
    },
    onchange () {
      this.$emit('change-value', this.model, this.unique)
    }
  }
}
</script>

<style lang="pcss">
@namespace hd {
  @b input {
    display: flex;
    align-items: center;
    font-size: 32px;
    padding: 25px 20px;
    line-height: 1.5;
    border-bottom: 1px solid #d9d9d9;
    background-color: #fff;

    @e label {
      width: 160px;
      flex-shrink: 0;
      color: #666;
    }

    @e core {
      flex-grow: 1;
      border: none;
      outline: none;
      appearance: none;
      font-size: inherit;
      color: inherit;
      line-height: 1.5;
    }

    @e clear {
      padding-left: 10px;
    }
  }
}
</style>



